.Container {
  border-radius: 16px;
  background-color: #FFFFFF;
  padding: 1em;
  margin-right: 2em;
  margin-bottom: 2em;
}

.Title {
  display: flex;

  h1 {
    color: #1A496B;
    font-family: Arial, sans-serif;
    font-size: 56px;
    line-height: 65px;
    margin: 0;
    font-weight: normal;
  }

  h2 {
    color: #29343D;
    font-family: Roboto, sans-serif;
    font-size: 20px;
    letter-spacing: 1.25px;
    line-height: 24px;
    margin: 8px 5px 0;
    font-weight: normal;
  }
}


.Content {
  color: #29343D;
  font-size: 16px;
  font-family: Roboto, sans-serif;
  font-weight: 300;
}
