@import "./style/variables.scss";

.App {
  margin: 3em auto;
  padding: 15px;
  color: $dark;
  font-family: Roboto, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  border-radius: 16px;
  background-color: #F4F8FB;
  //box-shadow: inset 0 8px 0 0 #1A496B;
  max-width: 688px;
  display: grid;
  grid-template-columns: 35% 65%;
  grid-template-rows: 10% 30% 30% 30%;
  grid-template-areas: "Header Header" "Step1 File" "Step2 Price" "Step3 Submit";
}

.Header {
  grid-area: Header;

  h3 {
    color: #444444;
    font-family: Arial, sans-serif;
    font-size: 24px;
    line-height: 28px;
    text-align: center;
    text-transform: uppercase;
    font-weight: normal;
  }
}

.Step1 {
  grid-area: Step1;
}

.Step2 {
  grid-area: Step2;
}

.Step3 {
  grid-area: Step3;
}

.UploadingContainer {
  width: 100%;
  min-height: 300px;
}

.UploadMessage {
  text-align: center;
  line-height: 300px;

}

.FileContainer {


}


.FileInfo {
  grid-area: File;
  cursor: pointer;
  display: flex;
  align-items: center;
  padding-top: 15px;
}

.Price {
  grid-area: Price;
}

.PriceTime {
  display: flex;
  justify-content: space-evenly;
}


.Help {
  grid-area: Help;
  border-top: 2px solid $dark;
  text-align: center;
}

.Slider {
  grid-area: Slider;
}

.Continue {
  grid-area: Submit;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Error {
  color: $pink;
  text-align: center;
  margin: 20px;
}
