@import "../style/variables";
@import "../style/animations";

.DragPrompt {
  border: 1px dashed #1A496B;
  border-radius: 16px;
  color: #444444;
  font-size: 10px;
  line-height: 11px;
  font-style: italic;
  font-family: Roboto, sans-serif;
  text-align: center;
  padding: 16px;
}

.Accept .DragPrompt {
  background: #1A496B;
  color: white;
  @include flash(infinite, $duration: 5s);
}

.Button {
  transition: all 0.2s ease-in;
  border-radius: 8px;
  background-color: #1A496B;
  margin-right: 24px;

  &:hover {
    background-color: #1d648b;
  }

  p {
    color: rgba(255, 255, 255, 0.7);
    font-family: Roboto, sans-serif;
    font-size: 12px;
    font-weight: 300;
    letter-spacing: 0.7px;
    line-height: 14px;
    text-align: center;
    margin: 0;
    padding: 8px 32px;
  }
}

.Grow {
  flex-grow: 1;
  text-align: center;

  color: #444444;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 300;
  letter-spacing: 1px;
  line-height: 19px;

}



.Reject {
  position: relative;
  background: white;
  border: 3px dashed $pink;
  border-radius: 3px;

  &:after {
    content: "x";
    position: absolute;
    color: $pink;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    font-size: 150px;
    line-height: 300px;
    text-align: center;
    @include flash(infinite);
  }
}

.Error {
  color: $pink;
  text-align: center;
  margin: 20px;
}

.ProgressHolder {
  border: 1px solid $pink;
  width: 200px;
  height: 1em;
  margin: 100px auto 10px;
}

.ProgressBar {
  background: $pink;
  display: block;
  height: 100%;
}

.Uploading {
  text-align: center;
  color: $pink;
  @include flash(infinite, 5s);
}
